<template>
  <div id="echart">
    <div style="padding:20px"><button @click="intPage()">点击加载图表</button></div>
    <div>
      <ul>
        <!-- <li v-for="ml in menuList">{{ml.name}}</li> -->
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose">
          <el-submenu v-bind:index="(key+1).toString()" v-for="(ml, key) in menuList" :key="key">
            <template slot="title">
              <span>{{ml.name}}</span>
            </template>
            <el-menu-item-group v-for="(ml2, key2) in ml.childList" :key="key2">
              <el-menu-item @click="routerTo(ml2)" v-bind:index="(key+1).toString() + (key2+1).toString()">{{ml2.name}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </ul>

    </div>
    <el-row>
      <el-col :span="24">
        <div class="panel" v-show="active">
          <div class="panel-heading">头部信息<input type="text" placeholder="搜索" v-model="searchName"></div>
          <div class="panel-body">
            <div class="demo-table">
              <el-table border
                :data="fileterList"
                height="600"
                stripe
                style="width: 100%">
                <el-table-column
                  prop="name"
                  label="姓名"
                  width="180">
                </el-table-column>
                <el-table-column
                  prop="desc"
                  label="备注">
                </el-table-column>
                <el-table-column
                  prop="btnList"
                  label="操作"
                  width="300">
                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div class="panel" v-show="!active">
          <div class="panel-heading">百度图标</div>
          <div class="panel-body">
            <div id="barone">图表显示区域</div>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
let $ = require('jquery/dist/jquery')
let echarts = require('echarts/lib/echarts')
// 引入柱状图组件
require('echarts/lib/chart/bar')
// 引入提示框和title组件
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')
export default {
  name: 'echart',
  data () {
    return {
      menu: [],
      maindata: [],
      menuList: [],
      searchName: '',
      active: true,
      option: {}
    }
  },
  mounted () {
    var a = $('#barone').text()
    console.log(a)
    // 测试数据
    this.$api.promise({
      method: 'POST',
      url: 'demo',
      data: {'name': '12345'}
    })
    .then(res => {
      if (res.data.code > 0) {
        this.maindata = res.data.list
      } else {
        this.$message.error(res.data.desc)
      }
    })
    .catch((err) => {
      this.fullscreenLoading = false
      this.$message.error('接口异常：' + err)
    })

    // 菜单数据
    this.$api.promise({
      method: 'POST',
      url: 'menu',
      data: {'name': '12345'}
    })
    .then(res => {
      if (res.data.code > 0) {
        this.menuList = res.data.list.filter(ls => {
          return ls.parentResId === -1
        })
        this.menuList.forEach((val, key) => {
          // this.$set(this.menuList[key], 'show', false)
          res.data.list.forEach((val2, key2) => {
            if (val2.parentResId === val.resId) {
              if (!Array.isArray(val.childList)) {
                val.childList = []
              }
              val.childList.push(val2)
            }
          })
        })
        console.dir(this.menuList)
      } else {
        this.$message.error(res.data.desc)
      }
    })
    .catch((err) => {
      this.fullscreenLoading = false
      this.$message.error('接口异常：' + err)
    })
  },
  computed: {
    fileterList: function () {
      return this.maindata.filter(list => {
        return list.name.match(this.searchName)
      })
    }
  },
  methods: {
    routerTo (item) {
      // console.dir(item)
      // console.log(this.$router)
      this.$router.push({ path: '/table' })
    },
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    intPage () {
      this.active = !this.active
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById('barone'))
      // 绘制图表
      myChart.setOption({
        // color:['#ccc'],
        title: { text: 'ECharts 入门示例' },
        tooltip: {},
        xAxis: {
          data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      })
    }
  }
}
</script>
<style>
#barone {
    width: 600px;
    height: 400px;
    margin: 0px auto;
}
</style>






